import { useState } from 'react';
import './Game.css';

function Game() {
    const [ action, setAction ] = useState('circle');
    const [ field, setField ] = useState([
        {
            id: 0,
            isEmpty: null
        },
        {
            id: 1,
            isEmpty: null
        },
        {
            id: 2,
            isEmpty: null
        },
        {
            id: 3,
            isEmpty: null
        },
        {
            id: 4,
            isEmpty: null
        },
        {
            id: 5,
            isEmpty: null
        },
        {
            id: 6,
            isEmpty: null
        },
        {
            id: 7,
            isEmpty: null
        },
        {
            id: 8,
            isEmpty: null
        },
    ]);
    const [ win, setWin ] = useState(null);
    const [ records, setRecords ] =useState([0, 0])
    const [ standoff, setStandoff ] = useState(false)
    const ifAction = (event) => {
        const el = event.target.id;
        const arr = field;
        arr[el].isEmpty = action;
        setField(arr);

        if(action === 'circle'){
            setAction('cross');
        } else {
            setAction('circle');
        }
        //Победа
        if(field[0].isEmpty === 'circle' && field[4].isEmpty === 'circle' && field[8].isEmpty === 'circle'){setWin('circle')}
        if(field[2].isEmpty === 'circle' && field[4].isEmpty === 'circle' && field[6].isEmpty === 'circle'){setWin('circle')}
        if(field[0].isEmpty === 'circle' && field[1].isEmpty === 'circle' && field[2].isEmpty === 'circle'){setWin('circle')}
        if(field[3].isEmpty === 'circle' && field[4].isEmpty === 'circle' && field[5].isEmpty === 'circle'){setWin('circle')}
        if(field[6].isEmpty === 'circle' && field[7].isEmpty === 'circle' && field[8].isEmpty === 'circle'){setWin('circle')}

        if(field[0].isEmpty === 'cross' && field[4].isEmpty === 'cross' && field[8].isEmpty === 'cross'){setWin('cross')}
        if(field[2].isEmpty === 'cross' && field[4].isEmpty === 'cross' && field[6].isEmpty === 'cross'){setWin('cross')}
        if(field[0].isEmpty === 'cross' && field[1].isEmpty === 'cross' && field[2].isEmpty === 'cross'){setWin('cross')}
        if(field[3].isEmpty === 'cross' && field[4].isEmpty === 'cross' && field[5].isEmpty === 'cross'){setWin('cross')}
        if(field[6].isEmpty === 'cross' && field[7].isEmpty === 'cross' && field[8].isEmpty === 'cross'){setWin('cross')}

        //Ничья
        const GameResult = field.find((el, i) => el.isEmpty == null) || true

        if(GameResult === true) {
            setStandoff(true)
        }
    }
    const newGame = (btn) => {
        console.log(btn)
        const newArr = field.map((el, i) => ({id: i, isEmpty: null}))
        setField(newArr)
        setWin(null)

        if (btn === 'newgame'){
            if(action === 'circle') {
                setRecords([records[0], records[1]+=1])
                console.log(records)
            } else  {
                setRecords([records[0]+=1, records[1]])
                console.log(records)
            }
        } else {
            setStandoff(false)
        }
    }
  return (
      <>
          <h1>Крестики нолики</h1>
        <div className="Game">
        {field.map((fld) => (
            <div key={fld.id} id={fld.id} className="field" onClick={(event) => ifAction(event)}>
                {
                    fld.isEmpty === 'circle' ? (
                        <div className="circle">
                            <img src="data:image/svg+xml;base64," alt="circle" />
                        </div>
                    ) : fld.isEmpty === 'cross' ? (
                        <div className="cross">
                            <img src="data:image/svg+xml;base64," alt="cross" />
                        </div>
                    ) : null
                }
            </div>
        ))}
        {win ? (
            <div className="winner">
                <p>{`Победитель: ${win === 'circle' ? 'Нолик' : 'Крестик'}`}</p>
                <button onClick={() => newGame('newgame')}>Новая игра</button>
            </div>
        ) : null}
        {standoff ? (
            <div className="standoff">
                <p>Ничья</p>
                <button onClick={() => newGame('standoff')}>Новая игра</button>
            </div>
        ) : null}
        <div className="record-table">
            <p>Таблица результатов:</p>
            <div>
                <div>{`Крестики: ${records[1]}`}</div>
                <div>{`Нолики: ${records[0]}`}</div>
            </div>
        </div>
    </div>
      </>
  );
}

export default Game;
